import React from 'react';
import { TextInput as RNTextInput } from 'react-native';
import dayjs from 'dayjs';
var customParseFormat = require('dayjs/plugin/customParseFormat');
dayjs.extend(customParseFormat);

import { withThemeProps, createBox } from '../../restyle';
import { isNumber } from '../../utils';
import { useUpdateEffect } from '../../hooks';
import { applyMask, removeMask } from './mask'; //@otw/mask

import Flex from '../Flex';
import Touchable from '../Touchable';
import Icon from '../Icon';

const StyledTextInput = createBox(RNTextInput);

const getLinesByString = (string) => {
  let array = string.split(/\r*\n/);
  return array.length;
};

const PRE = {
  phone: {
    mask: 'a.. (...) .......',
    validators: {
      'a': /[+-+]/,
      '.': /[0-9]/,
    },
  },
  date: {
    mask: 'Dd.Mm.yYYY',
    validators: {
      D: /[0-3]/,
      d: /[0-9]/,
      M: /[01]/,
      m: /[0-9]/,
      y: /[12]/,
      Y: /[0-9]/,
      H: /[0-2]/,
      h: /[0-9]/,
      T: /[0-5]/,
      t: /[0-9]/,
    },
  },
};

const MASKS = {
  number: {
    getValue: (v) => {
      v = `${v}`.replace(',', '.').replace(/[^0-9.]|\.(?=.*\.)/g, '');
      return v;
    },
    parseValue: (v, onChange) => {
      console.log({ v });
      if (onChange) {
        if (v.length > 0 && onChange) {
          onChange(v.indexOf('.') > -1 ? parseFloat(v) : parseInt(v));
        } else if (onChange) {
          onChange(null);
        }
      }
    },
    props: {
      keyboardType: 'decimal-pad',
      autoCapitalize: 'none',
    },
  },
  phone: {
    getValue: (v) => {
      if (v.length === 1 && v !== '+') v = `+${v}`;
      const { mask, validators } = PRE['phone'];

      const rawValue = removeMask({ mask, validators, value: v });
      const maskedValue = applyMask({ mask, validators, value: rawValue });
      return maskedValue;
    },
    parseValue: (v, onChange) => {
      const { mask, validators } = PRE['phone'];
      if (onChange) onChange(removeMask({ mask, validators, value: v }));
    },
    placeholder: '+49 (123) 45678901',
  },
  date: {
    getValue: (v) => {
      const { mask, validators } = PRE['date'];
      if (v instanceof Date) {
        v = dayjs(v).format('DD.MM.YYYY');
      } else {
        v = `${v}`;
      }

      const rawValue = removeMask({ mask, validators, value: v });
      const maskedValue = applyMask({ mask, validators, value: rawValue });
      return maskedValue;
    },
    parseValue: (v, onChange) => {
      const isValid = dayjs(v, 'DD.MM.YYYY').format('DD.MM.YYYY') === v;
      if (onChange && isValid) {
        onChange(dayjs(v, 'DD.MM.YYYY').toDate());
      } else if ((onChange, v.length === 0)) {
        onChange(null);
      }
    },
    props: {
      keyboardType: 'number-pad',
      autoCapitalize: 'none',
    },
    placeholder: 'dd.mm.yyyy',
  },
  time: {
    getValue: (v) => {
      v = v.replace(/^([0-1]?[0-9]|2[0-3]):[0-5][0-9]$/, '');
      if (v === '') return null;
      return v;
    },
  },
};

const TextInput = ({
  theme,
  as,
  value,
  onChange,
  setFocus,
  onFocus,
  onBlur,
  placeholder,
  placeholderColor = 'placeholder',
  color = 'text',
  multiline,
  numberOfLines,
  size = 50,
  roundness,
  children,
  mask,
  secureTextEntry,
  ...rest
}) => {
  const maskObj = MASKS[mask];
  const [secure, setSecure] = React.useState(secureTextEntry);
  const [text, setText] = React.useState(
    value !== undefined && value !== null
      ? `${
          maskObj && maskObj.getValue && value ? maskObj.getValue(value) : value
        }`
      : ''
  );
  const textColor = theme.colors[color] || color;
  const placeholderTextColor =
    theme.colors[placeholderColor] || placeholderColor;

  const getLines = () => {
    const LinesByValue = getLinesByString(value || '');
    return numberOfLines >= LinesByValue ? numberOfLines : LinesByValue;
  };

  useUpdateEffect(() => {
    setText(
      value !== undefined && value !== null
        ? `${maskObj && maskObj.getValue ? maskObj.getValue(value) : value}`
        : ''
    );
  }, [value]);

  return (
    <Flex
      width="100%"
      alignItems="center"
      bg="input"
      py={multiline ? theme.globals.gap : undefined}
      borderRadius={isNumber(roundness) ? roundness : theme.globals.roundness}
      row
    >
      <StyledTextInput
        flex={1}
        width="100%"
        px={theme.globals.inputGap}
        height={multiline ? 'auto' : size}
        minHeight={size}
        value={text}
        as={as || undefined}
        onChangeText={(v) => {
          if (maskObj) {
            v = maskObj.getValue(v);
          }
          setText(v || '');
          if (maskObj && maskObj.parseValue) {
            v = maskObj.parseValue(v, onChange);
          } else {
            if (onChange) onChange(v);
          }
        }}
        placeholderTextColor={placeholderTextColor}
        underlineColorAndroid="transparent"
        onFocus={() => {
          if (setFocus) {
            setFocus(true);
          }
          if (onFocus) {
            onFocus();
          }
        }}
        onBlur={() => {
          if (setFocus) {
            setFocus(false);
          }
          if (onBlur) {
            onBlur();
          }
        }}
        multiline={multiline}
        numberOfLines={multiline ? getLines() : undefined}
        scrollEnabled={false}
        secureTextEntry={secure}
        hideUnderline
        font="default"
        placeholder={
          maskObj && maskObj.placeholder ? maskObj.placeholder : placeholder
        }
        webStyle={{
          outlineStyle: 'none',
          borderColor: 'transparent',
        }}
        {...rest}
        {...(MASKS[mask] ? MASKS[mask].props : {})}
        style={{
          margin: 0,
          borderWidth: 0,
          fontFamily: theme.globals.fontFamily,
          color: textColor,
          ...theme.fonts.default,
          ...(rest.style || {}),
        }}
      />

      {children && (
        <Flex height={size} pr={theme.globals.gap} justifyContent="center">
          {React.Children.only(
            React.cloneElement(children, {
              value,
              onChange,
            })
          )}
        </Flex>
      )}
      {secureTextEntry && (
        <Touchable
          px={theme.globals.gap}
          height={size}
          onPress={() => setSecure(!secure)}
          flexCenter
        >
          <Icon
            key={secure}
            name={secure ? 'eye' : 'eyeOff'}
            size={size / 2.66}
            animate
          />
        </Touchable>
      )}
    </Flex>
  );
};

export default withThemeProps(TextInput, 'TextInput');
